@use '../foundation' as *;

.p-facility {
}

.p-facility__inner {
}

.p-facility__wrap {
}

.p-facility-title__content {
    display: flex;
    margin-bottom: rem(35);
}

.p-facility__title {
    // margin-bottom: rem(20);
}

.p-facility__update {
    font-weight: 400;
    font-size: rem(16);
    display: flex;
    align-items: flex-end;
    margin-left: rem(30);
    @include mq(md) {
        font-size: rem(20);
    }
}

.p-facility__list {
    margin-bottom: rem(37);
    @include mq(md) {
        margin-bottom: rem(37);
        display: flex;
        flex-wrap: wrap;
        justify-content: space-between;
    }
}

.p-facility-list__item {
    @include mq(md) {
        max-width: calc(33.333% - #{rem(20)} * 2 / 3);
        width: 100%;
    }
}

.p-facility-list__item + .p-facility-list__item {
    margin-top: rem(18);
    @include mq(md) {
        margin-top: 0;
        margin-left: rem(18);
    }
}

.p-facility-list__item:nth-of-type(3n + 1) {
    @include mq(md) {
        margin-left: 0;
    }
}

.p-facility-list__item:nth-of-type(n + 4) {
    @include mq(md) {
        margin-top: rem(38);
    }
}

.p-facility-list__item a {
    display: block;
    height: 100%;
    // background-color: #fff;
    // box-shadow: 0 0 rem(40) 0 rgba(0, 0, 0, 0.05);
}

.p-facility-list__item-img {
    // height: per(243, 365);
    margin-bottom: rem(15);
    @include mq(md) {
        margin-bottom: rem(24);
    }
    img {
        width: 100%;
        object-fit: cover;
        aspect-ratio: 365/243;
        @include mq(md) {
            aspect-ratio: 416/377;
        }
    }
}

.p-facility-list__item-content {
}

.p-facility-list__item-title {
    font-weight: 700;
    margin-bottom: rem(15);
    font-size: rem(20);
    @include mq(md) {
        font-size: rem(20);
        margin-bottom: rem(12);
    }
}

.p-facility-list__item-text {
    // width: per(309, 417);
}

.p-facility__info-text {
    font-size: rem(24);
    line-height: calc(29 / 24);
    font-weight: 400;
    margin-bottom: rem(30);
    @include mq(md) {
        margin-bottom: rem(60);
        // margin-left: rem(77);
    }
}

