.inner {
}

.p-facility-hero__wrap {
    background-color: $color-background;
    padding: rem(30);
    border-radius: rem(20);
    @include mq(md) {
        padding: rem(50) ;
    }
}

.p-facility-hero__title {
    font-weight: bold;
    font-size: rem(24);
    margin-bottom: rem(20);
    text-align: center;
    @include mq(md) {
        font-size: rem(32);
        margin-bottom: rem(32);
    }
}

.p-facility-hero__text {
    font-size: rem(16);
    font-weight: 400;
    line-height: 1.6;
    margin: 0 auto;
    @include mq(md) {
        width: per(780, 1280);
        font-size: rem(20);
    }
}
